import Card from "../UI/Card";
import {useEffect, useState} from "react";
import {Link} from "react-router-dom";
import axios from "axios";



const Ohota = () => {

    const [data, setData] = useState([]);

    //let url = 'http://localhost/MISU-cattle/web/index.php?r=api%2Fget-data';
    let url = 'http://18.197.157.198/index.php?r=api%2Fget-data';

    useEffect(() => {
        axios.get(url)
            .then(function (response) {
                console.log(response);
                setData(response.data);
            })
            .catch(function (error) {
                console.log(error.response.data);
            });
    }, []);


     const result = data.map((item) => (
        <Link key={item.id} className='item_menu' to={`/chart/${item.id}`} >
                <Card className="green" key={item.id}>
                    <h3>Device id - {item.id}; </h3>
                    <span>  </span>
                    <h3>Назва - {item.device_name}; </h3>
                </Card>
            </Link>
    ));


    return (
        <>
            <div className='cow' ></div>
            { result }
        </>
    )
}

export default Ohota