import React, {useState} from 'react';
import {useParams} from "react-router-dom";
import { Line } from 'react-chartjs-2';
import {useEffect} from "react";
import Card from "../UI/Card";
import axios from "axios";
import {
    Chart as ChartJS,
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Filler,
    Legend,
} from 'chart.js';


ChartJS.register(
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Filler,
    Legend
);


    export function Charts()
    {
        const {device_id} = useParams();

        const [arrayData, setArrayData] = useState([]);

        let url = 'http://18.197.157.198/index.php?r=api%2Fbuild-chart';
        //let url = 'http://localhost/MISU-cattle/web/index.php?r=api%2Fbuild-chart';

        useEffect(() => {
            axios.post(url, {device_id: device_id})
                .then(function (response) {
                    console.log(response.data);
                    setArrayData(response.data);
                })
                .catch(function (error) {
                    console.log(error.response.data);
                });

        }, [device_id]);


        let acceleration_sting = arrayData?.map(item => item['acceleration']);
        let acceleration_array = acceleration_sting.map((item)=> (item.split(' ')))

        const acceleration = {
            labels: arrayData?.map(item => item['date']),
            datasets: [
                {
                    label: 'acceleration_x',                    //props.label,
                    data: acceleration_array.map((item)=> (item[0])),
                    borderColor: 'rgba(124,22,78,0.95)',
                    backgroundColor: 'rgba(201,127,156,0.5)',
                },
                {
                    label: 'acceleration_y',                    //props.label,
                    data: acceleration_array.map((item)=> (item[1])),
                    borderColor: 'rgba(21,147,42,0.5)',
                    backgroundColor: 'rgba(120,136,61,0.95)',
                },
                {
                    label: 'acceleration_z',                    //props.label,
                    data: acceleration_array.map((item)=> (item[2])),
                    borderColor: 'rgba(22,66,210,0.5)',
                    backgroundColor: 'rgba(96,145,194,0.95)',
                },
            ],
        }

        let rotation_sting = arrayData?.map(item => item['rotation']);
        let rotation_array = rotation_sting.map((item)=> (item.split(' ')))

        const rotation = {
            labels: arrayData?.map(item => item['date']),
            datasets: [
                {
                    label: 'rotation_x',                    //props.label,
                    data: rotation_array.map((item)=> (item[0])),
                    borderColor: 'rgba(124,22,78,0.95)',
                    backgroundColor: 'rgba(201,127,156,0.5)',
                },
                {
                    label: 'rotation_y',                    //props.label,
                    data: rotation_array.map((item)=> (item[1])),
                    borderColor: 'rgba(21,147,42,0.5)',
                    backgroundColor: 'rgba(120,136,61,0.95)',
                },
                {
                    label: 'rotation_z',                    //props.label,
                    data: rotation_array.map((item)=> (item[2])),
                    borderColor: 'rgba(22,66,210,0.5)',
                    backgroundColor: 'rgba(96,145,194,0.95)',
                },
            ],
        }

        let gravity_sting = arrayData?.map(item => item['gravity']);
        let gravity_array = gravity_sting.map((item)=> (item.split(' ')))

        const gravity = {
            labels: arrayData?.map(item => item['date']),
            datasets: [
                {
                    label: 'gravity_x',                    //props.label,
                    data: gravity_array.map((item)=> (item[0])),
                    borderColor: 'rgba(124,22,78,0.95)',
                    backgroundColor: 'rgba(201,127,156,0.5)',
                },
                {
                    label: 'gravity_y',                    //props.label,
                    data: gravity_array.map((item)=> (item[1])),
                    borderColor: 'rgba(21,147,42,0.5)',
                    backgroundColor: 'rgba(120,136,61,0.95)',
                },
                {
                    label: 'gravity_z',                    //props.label,
                    data: gravity_array.map((item)=> (item[2])),
                    borderColor: 'rgba(22,66,210,0.5)',
                    backgroundColor: 'rgba(96,145,194,0.95)',
                },
            ],
        }

        const options = {
            responsive: true,
            plugins: {
                legend: {
                    position: 'top',
                },
                title: {
                    display: true,
                    text: 'Графік для девайсу №'+device_id,
                },
            },
        };

        return (
            <div className='for_chart'>
                <Card className="chart">
                    <Line options={options} data={acceleration} />
                </Card>
                <Card className="chart">
                    <Line options={options} data={rotation} />
                </Card>
                <Card className="chart">
                   <Line options={options} data={gravity} />
                </Card>
            </div>
        );
    }

export default Charts;