import {NavLink, Outlet} from "react-router-dom";
import Card from "../UI/Card";

import './../components.css'
import {Button} from "@mui/material";


const Layout = () => {


    return (
        <>
            <div className='wrap'>
                <Card className='card'>
                    <Button>
                        <NavLink className='item_menu' to="/">Головна</NavLink>
                    </Button>
                    <Button>
                        <NavLink className='item_menu' to="/ohota">Охота</NavLink>
                    </Button>
                    <Button>
                        <NavLink className='item_menu' to="/about">про ...</NavLink>
                    </Button>
                </Card>
                <Outlet/>
            </div>
            <footer className='footer'>MISU_Cattle - 2022</footer>
        </>
    )
}

export default Layout;